h1 {
    color: green;
    font-style: italic;
    margin: 10px 0 0 0;
}

.note {
    color: rgb(94, 31, 31);
    padding-top: 3px;
    font-size: 15px;
}

.note button {
    margin-left: 15px;
}

.btn {
    color: white;
    width: 100px;
    margin-left: 15px;
    border-radius: 5px;
    padding: 5px 10px;
}

.btn-info {
    background: rgb(148, 155, 149);
    border-color:rgb(91, 94, 92);
    color: #000;
}

.btn-secondary {
    background: rgb(16, 180, 30);
    border-color:rgb(7, 119, 16);
}

.btn-danger {
    background: rgb(185, 61, 4);
    border-color: rgb(116, 39, 4);
}

.error {
    color: white;
    font-size: 18px;
    border-style: solid;
    border-radius: 10px;
    padding: 5px;
    padding-left: 30px;
    margin-bottom: 5px;
    width: 70%;
}

.error-info {
    background: rgb(3, 173, 60);
}

.error-wrng {
    background: rgb(182, 4, 4);
    color: #000;
}

.error-blank {
    color: rgb(58, 56, 56);
    background: rgb(165, 159, 159);
}

.btn-submit {
    width: 200px;
    padding: 5px;
}